import React, { useState, useEffect } from 'react';
import { GameSetup } from './GameSetup';
import { QRCode } from './QRCode';
import { webrtcManager } from '../utils/webrtc';
import { gameStateManager } from '../utils/gameState';

const ROLES = {
  VILLAGER: 'villager',
  WEREWOLF: 'werewolf',
  SEER: 'seer',
  BODYGUARD: 'bodyguard'
};

const ROLE_DESCRIPTIONS = {
  [ROLES.VILLAGER]: 'A regular villager. Your goal is to identify and eliminate the werewolves.',
  [ROLES.WEREWOLF]: 'A werewolf. Each night, you can eliminate one player. Work with other werewolves to eliminate all villagers.',
  [ROLES.SEER]: 'A seer. Each night, you can check one player\'s role to determine if they are a werewolf.',
  [ROLES.BODYGUARD]: 'A bodyguard. Each night, you can protect one player from being eliminated. You cannot protect the same player two nights in a row.'
};

const PHASES = {
  SETUP: 'setup',
  NIGHT: 'night',
  DAY: 'day',
  VOTING: 'voting',
  GAME_OVER: 'game_over'
};

export const Game = () => {
  const [gameState, setGameState] = useState({
    phase: PHASES.SETUP,
    players: [],
    currentPlayer: null,
    votes: {},
    gameOver: false,
    winner: null,
    isGameMaster: false,
    gameCode: null,
    playerRole: null,
    playerName: null,
    lastProtectedPlayer: null
  });

  useEffect(() => {
    // Load game state from localStorage
    const savedState = gameStateManager.loadGameState();
    if (savedState) {
      setGameState(prev => ({
        ...prev,
        ...savedState
      }));
    }

    // Set up WebRTC message handler
    webrtcManager.setMessageCallback(handleWebRTCMessage);
    
    // Start checking for new connections
    webrtcManager.startConnectionCheck();

    // Cleanup on unmount
    return () => {
      webrtcManager.cleanup();
    };
  }, []);

  const handleWebRTCMessage = (message) => {
    switch (message.type) {
      case 'GAME_STATE_UPDATE':
        setGameState(prev => ({
          ...prev,
          ...message.data
        }));
        break;
      case 'PLAYER_JOINED':
        setGameState(prev => ({
          ...prev,
          players: [...prev.players, message.data]
        }));
        break;
      case 'GAME_ENDED':
        alert(message.data);
        setGameState(prev => ({
          ...prev,
          phase: PHASES.SETUP,
          gameOver: true
        }));
        break;
      case 'PLAYER_LEFT':
        setGameState(prev => ({
          ...prev,
          players: prev.players.filter(p => p.id !== message.data)
        }));
        break;
    }
  };

  const startGame = async (config) => {
    const initialState = gameStateManager.initializeGame(config);
    const gameCode = await webrtcManager.createGameMasterConnection();
    
    setGameState(prev => ({
      ...prev,
      ...initialState,
      isGameMaster: true,
      gameCode,
      phase: PHASES.NIGHT
    }));
  };

  const joinGame = async ({ playerName, gameCode }) => {
    const role = gameStateManager.assignRoleToPlayer(gameCode, playerName);
    if (!role) {
      alert('Game is full or not found!');
      return;
    }

    await webrtcManager.connectToGameMaster(gameCode);
    
    setGameState(prev => ({
      ...prev,
      playerName,
      gameCode,
      playerRole: role,
      phase: PHASES.NIGHT
    }));

    // Notify game master
    webrtcManager.broadcastMessage({
      type: 'PLAYER_JOINED',
      data: { name: playerName, role, id: webrtcManager.peers.get(gameCode).id }
    });
  };

  const handleVote = (voterId, targetId) => {
    const newVotes = {
      ...gameState.votes,
      [voterId]: targetId
    };

    setGameState(prev => ({
      ...prev,
      votes: newVotes
    }));

    if (gameState.isGameMaster) {
      gameStateManager.saveGameState({
        ...gameState,
        votes: newVotes
      });
      webrtcManager.broadcastMessage({
        type: 'GAME_STATE_UPDATE',
        data: { votes: newVotes }
      });
    }
  };

  const handleNightAction = (playerId, targetId, action) => {
    // Handle night phase actions (werewolf kill, seer check, doctor heal)
  };

  const nextPhase = () => {
    if (!gameState.isGameMaster) return;

    const phaseOrder = [PHASES.NIGHT, PHASES.DAY, PHASES.VOTING];
    const currentIndex = phaseOrder.indexOf(gameState.phase);
    const nextPhase = phaseOrder[(currentIndex + 1) % phaseOrder.length];

    const newState = {
      ...gameState,
      phase: nextPhase
    };

    setGameState(newState);
    gameStateManager.saveGameState(newState);
    webrtcManager.broadcastMessage({
      type: 'GAME_STATE_UPDATE',
      data: { phase: nextPhase }
    });
  };

  if (gameState.phase === PHASES.SETUP) {
    return (
      <GameSetup 
        onGameStart={startGame}
        onJoin={joinGame}
      />
    );
  }

  return (
    <div className="game-container">
      <div className="game-header">
        <h1>Lupus in Tabula</h1>
        <div className="game-info">
          <div className="game-code-display">
            Game Code: <strong>{gameState.gameCode}</strong>
          </div>
          <div className="game-phase">
            Current Phase: <strong>{gameState.phase}</strong>
          </div>
        </div>
        {gameState.isGameMaster && gameState.gameCode && (
          <QRCode gameCode={gameState.gameCode} />
        )}
      </div>

      {gameState.isGameMaster ? (
        <div className="game-master-view">
          <div className="game-controls">
            <h2>Game Master Controls</h2>
            <button onClick={nextPhase} className="phase-btn">
              Next Phase
            </button>
          </div>
          <div className="player-list">
            <h3>Players</h3>
            {gameState.players.map(player => (
              <div key={player.name} className="player-item">
                <span>{player.name}</span>
                <span className="player-role">{player.role}</span>
              </div>
            ))}
          </div>
        </div>
      ) : (
        <div className="player-view">
          <h2>Welcome, {gameState.playerName}!</h2>
          <div className="role-info">
            <p className="role-title">Your role: {gameState.playerRole}</p>
            <p className="role-description">{ROLE_DESCRIPTIONS[gameState.playerRole]}</p>
          </div>
          <p>Current phase: {gameState.phase}</p>
          
          {gameState.phase === PHASES.NIGHT && (
            <div className="night-action">
              {gameState.playerRole === ROLES.WEREWOLF && (
                <h3>Choose a player to eliminate</h3>
              )}
              {gameState.playerRole === ROLES.SEER && (
                <h3>Choose a player to check</h3>
              )}
              {gameState.playerRole === ROLES.BODYGUARD && (
                <h3>Choose a player to protect</h3>
              )}
              {/* Add player selection UI */}
            </div>
          )}
          
          {gameState.phase === PHASES.VOTING && (
            <div className="voting-action">
              <h3>Vote to eliminate a player</h3>
              {/* Add voting UI */}
            </div>
          )}
        </div>
      )}
    </div>
  );
}; 