class WebRTCManager {
  constructor() {
    this.peers = new Map();
    this.messageCallback = null;
    this.isGameMaster = false;
    this.gameCode = null;
  }

  setMessageCallback(callback) {
    this.messageCallback = callback;
  }

  async createGameMasterConnection() {
    this.isGameMaster = true;
    this.gameCode = this.generateGameCode();
    return this.gameCode;
  }

  async connectToGameMaster(gameCode) {
    this.gameCode = gameCode;
    this.isGameMaster = false;
    
    // Create a data channel for communication
    const peer = new SimplePeer({
      initiator: true,
      trickle: false
    });

    this.setupPeerHandlers(peer);
    this.peers.set(gameCode, peer);

    // Generate connection offer
    const offer = await new Promise((resolve) => {
      peer.on('signal', (data) => {
        if (data.type === 'offer') {
          resolve(data);
        }
      });
    });

    // Store the offer in localStorage for the game master to find
    this.storeConnectionOffer(gameCode, offer);
  }

  async handleIncomingConnection(peerId, offer) {
    const peer = new SimplePeer({
      initiator: false,
      trickle: false
    });

    this.setupPeerHandlers(peer);
    this.peers.set(peerId, peer);

    // Set the remote description
    await peer.signal(offer);

    // Generate answer
    const answer = await new Promise((resolve) => {
      peer.on('signal', (data) => {
        if (data.type === 'answer') {
          resolve(data);
        }
      });
    });

    // Store the answer in localStorage
    this.storeConnectionAnswer(peerId, answer);
  }

  setupPeerHandlers(peer) {
    peer.on('connect', () => {
      console.log('Peer connection established');
    });

    peer.on('data', (data) => {
      if (this.messageCallback) {
        const message = JSON.parse(data);
        this.messageCallback(message);
      }
    });

    peer.on('error', (err) => {
      console.error('Peer connection error:', err);
    });

    peer.on('close', () => {
      console.log('Peer connection closed');
    });
  }

  broadcastMessage(message) {
    const messageStr = JSON.stringify(message);
    this.peers.forEach(peer => {
      if (peer.connected) {
        peer.send(messageStr);
      }
    });
  }

  storeConnectionOffer(gameCode, offer) {
    const offers = JSON.parse(localStorage.getItem('webrtc_offers') || '{}');
    offers[gameCode] = offer;
    localStorage.setItem('webrtc_offers', JSON.stringify(offers));
  }

  storeConnectionAnswer(peerId, answer) {
    const answers = JSON.parse(localStorage.getItem('webrtc_answers') || '{}');
    answers[peerId] = answer;
    localStorage.setItem('webrtc_answers', JSON.stringify(answers));
  }

  checkForConnectionOffers() {
    if (!this.isGameMaster) return;

    const offers = JSON.parse(localStorage.getItem('webrtc_offers') || '{}');
    const gameOffer = offers[this.gameCode];

    if (gameOffer) {
      this.handleIncomingConnection(this.gameCode, gameOffer);
      // Clean up the offer
      delete offers[this.gameCode];
      localStorage.setItem('webrtc_offers', JSON.stringify(offers));
    }
  }

  checkForConnectionAnswers() {
    if (this.isGameMaster) return;

    const answers = JSON.parse(localStorage.getItem('webrtc_answers') || '{}');
    const gameAnswer = answers[this.gameCode];

    if (gameAnswer) {
      const peer = this.peers.get(this.gameCode);
      if (peer) {
        peer.signal(gameAnswer);
      }
      // Clean up the answer
      delete answers[this.gameCode];
      localStorage.setItem('webrtc_answers', JSON.stringify(answers));
    }
  }

  generateGameCode() {
    return Math.random().toString(36).substring(2, 8).toUpperCase();
  }

  startConnectionCheck() {
    // Check for new connections every second
    this.connectionInterval = setInterval(() => {
      this.checkForConnectionOffers();
      this.checkForConnectionAnswers();
    }, 1000);
  }

  stopConnectionCheck() {
    if (this.connectionInterval) {
      clearInterval(this.connectionInterval);
    }
  }

  cleanup() {
    this.stopConnectionCheck();
    this.peers.forEach(peer => {
      peer.destroy();
    });
    this.peers.clear();
  }
}

export const webrtcManager = new WebRTCManager(); 