const GAME_STATE_KEY = 'lupus_game_state';

export const gameStateManager = {
  saveGameState(state) {
    localStorage.setItem(GAME_STATE_KEY, JSON.stringify(state));
  },

  loadGameState() {
    const state = localStorage.getItem(GAME_STATE_KEY);
    return state ? JSON.parse(state) : null;
  },

  clearGameState() {
    localStorage.removeItem(GAME_STATE_KEY);
  },

  generateRoles(config) {
    const { totalPlayers, werewolves, seers, bodyguards } = config;
    const roles = [];
    
    // Add werewolves
    for (let i = 0; i < werewolves; i++) {
      roles.push('werewolf');
    }
    
    // Add seers
    for (let i = 0; i < seers; i++) {
      roles.push('seer');
    }
    
    // Add bodyguards
    for (let i = 0; i < bodyguards; i++) {
      roles.push('bodyguard');
    }
    
    // Fill remaining slots with villagers
    while (roles.length < totalPlayers) {
      roles.push('villager');
    }
    
    // Shuffle roles
    return roles.sort(() => Math.random() - 0.5);
  },

  assignRoleToPlayer(gameCode, playerName) {
    const state = this.loadGameState();
    if (!state) return null;

    // Find the first unassigned role
    const unassignedRoleIndex = state.roles.findIndex(role => !role.assignedTo);
    if (unassignedRoleIndex === -1) return null;

    // Assign the role to the player
    state.roles[unassignedRoleIndex].assignedTo = playerName;
    this.saveGameState(state);

    return state.roles[unassignedRoleIndex].type;
  },

  initializeGame(config) {
    const roles = this.generateRoles(config).map(type => ({
      type,
      assignedTo: null
    }));

    const initialState = {
      gameCode: config.gameCode,
      phase: 'setup',
      roles,
      players: [],
      votes: {},
      gameOver: false,
      winner: null,
      currentPlayer: null,
      lastProtectedPlayer: null // Track the last player protected by bodyguard
    };

    this.saveGameState(initialState);
    return initialState;
  }
}; 